
import axios,{ AxiosRequestConfig } from 'axios'

interface Pending {
    [key: string]: any
}

const pending: Pending = {}; // 请求队列

// 增加到等待队列
export const addPengding = (config: AxiosRequestConfig) => {
    const cancelKey = `${config.method}-${config.url}`;
    config.cancelToken = new axios.CancelToken((c: any) => {
        pending[cancelKey] = c;
    })
}

// 移出等待队列
export const removePending = (config: AxiosRequestConfig) => {
    const cancelKey = `${config.method}-${config.url}`;
    if (pending[cancelKey]) {
        let cancel = pending[cancelKey];
        cancel();
        delete pending[cancelKey];
    }
}

// 所有等待移出队列
export const cancelAllPening = () => {
    Object.values(pending).forEach((cancel: any) => {
        cancel();
    })
}
