// import axios from '@/libs/request';
import storageProxy from '@/utils/storageProxy';

export default {
    namespaced: true,
    state: storageProxy(
        {
            roleList: [
                {
                    name: '超级管理员',
                    key: 'super_admin',
                    disabled: false,
                },
                {
                    name: '管理员',
                    key: 'admin',
                    disabled: false,
                },
            ],
        },
        {
            prefix: 'user/',
        }
    ),
    getters: {},
    actions: {},
    mutations: {
        // 设置角色列表
        setRoleList(state: any, data: any) {
            state.roleList = data;
        },
    },
};
