const { apiConfig, env, idassEnvOptions } = require('@/envConfig');
import { addPengding, removePending } from './axiosPending';
import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios';
import { ElNotification } from 'element-plus';
// import router from '@/router';
import windowStorage from '@/utils/windowStorage';

// oidc不同请求域名单
const oidcApiList = [
    '/.well-known/openid-configuration',
    '/api/account/login',
    '/api/account/logout',
    '/api/account/error',
];
// idassEnvOptions[env].apiUrl;

// 创建实例时设置配置的默认值
const instance: AxiosInstance = axios.create({
    // baseURL: env == 'dev' ? '' : apiConfig[env],
    baseURL: apiConfig[env],
    headers: {},
    timeout: 15000,
});
// instance.defaults.headers['Content-Type'] = 'application/json;charset=UTF-8';
instance.defaults.withCredentials = true;
// 请求拦截
instance.interceptors.request.use((config: AxiosRequestConfig) => {
    const token = windowStorage.getSessionItem('token');
    if (token) {
        config.headers = config.headers || {};
        config.headers.Authorization = `Bearer ${token}` || '';
    }
    if (oidcApiList.indexOf(config.url as string) > -1) {
        config.baseURL = idassEnvOptions[env].apiUrl;
    }
    removePending(config);
    addPengding(config);
    return config;
});
// 响应拦截
instance.interceptors.response.use(
    (response: AxiosResponse) => {
        removePending(response);
        return response.data;
    },
    (error: object) => {
        return Promise.reject(error);
    }
);

export default (
    url: string,
    data: any = {},
    method: string = 'get',
    headers: any = null
) => {
    let config: AxiosRequestConfig = {};
    method = method.toLowerCase();
    method === 'get' || method === 'delete'
        ? (config = { params: { ...data } } || {})
        : (config = { data });
    if (headers) config.headers = { ...headers };
    return new Promise((reslove, reject) => {
        instance({
            url,
            method,
            headers,
            ...config,
        })
            .then((res: any) => {
                let { success, code, message, data, ...rest } = res;
                if (res.success) {
                    let resData = data;
                    Object.keys(rest).length > 0 &&
                        (resData = { data, ...rest });
                    reslove(resData);
                } else {
                    // Toast.fail({
                    //     message: res.message,
                    // });
                    // reject(res);
                    return Promise.reject(res);
                }
            })
            .catch((error: any) => {
                // Toast.clear();
                console.log(error, '失败信息');
                const hasRes: boolean = !!error.response;
                if (axios.isCancel(error)) {
                    console.log('取消请求');
                } else {
                    // 有响应的异常处理
                    if (hasRes) {
                        let { status, data } = error.response;
                        // token 过期处理
                        if (status === 401 || status === 403) {
                            const failMsg = data.error
                                ? data.error.message
                                : `会话超时`;
                            windowStorage.deleteSeesionItem('token');
                            // Toast.fail({
                            //     message: failMsg,
                            //     onClose: () => {
                            //         router.push({ name: 'home' });
                            //     },
                            // });
                        } else {
                            // Toast.fail({
                            //     message: `服务器异常`,
                            // });
                        }
                    } else {
                        let { code, message } = error;
                        let msg: string = '';
                        if (code === 'ECONNABORTED' && /timeout/i.test(message))
                            msg = '请求超时';
                        if (
                            code === 'ERR_NETWORK' &&
                            message === 'Network Error'
                        )
                            msg = '网络连接失败';
                        // Toast.fail({
                        //     message: msg || '请求失败',
                        // });
                        ElNotification({
                            title: '错误信息',
                            message: message,
                            type: 'error',
                        });
                    }
                }
            });
    });
};
