import { ref, watch } from 'vue';
import { useRouter } from 'vue-router';
import { deepClone } from '@/utils/toolFn';
export default {
  setup() {
    const router = useRouter();
    const routeMenu = ref([]);
    const currentActive = ref('');

    const changeActiveMenu = index => {
      index !== currentActive.value && (currentActive.value = index), router.push({
        name: index
      });
    };

    watch(() => router.currentRoute.value, newVal => {
      newVal.name && (currentActive.value = newVal.name);
    }, {
      immediate: true
    }); // 递归数组

    const recursionList = list => {
      let existNameList = [];
      let callbackArray = [];

      function recusionChildren(matched, result) {
        let currentRoute = matched.shift();
        if (!currentRoute) return result;
        let {
          meta,
          name,
          children
        } = currentRoute;

        if (existNameList.indexOf(name) === -1) {
          let routeInfo = {
            title: meta && meta.title || "",
            name: name,
            children: []
          };
          if (children && children.length > 0) meta && meta.hideMenuEntry ? recusionChildren(children, result) : routeInfo.children = recusionChildren(children, []);
          if (meta && !meta.hideMenu && !meta.hideMenuEntry) result.push(routeInfo);
          existNameList.push(name);
        }

        recusionChildren(matched, result);
        return result;
      }

      callbackArray = recusionChildren(list, []);
      return callbackArray;
    };

    let filterMatched = router.options.routes.map(({
      children,
      meta,
      name,
      path
    }) => ({
      children,
      meta,
      name,
      path
    }));
    routeMenu.value = recursionList(deepClone(filterMatched));
    return {
      routeMenu,
      currentActive,
      changeActiveMenu
    };
  }

};