import axios from '@/libs/request';
import storageProxy from '@/utils/storageProxy';
import windowStorage from '@/utils/windowStorage';
interface LoginForm {
    name: string;
    password: string;
}

export default {
    namespaced: true,
    state: storageProxy(
        {
            userInfo: {}, // 用户数据
            roleList: [
                {
                    name: '超级管理员',
                    key: 'super_admin',
                    disabled: false,
                },
                {
                    name: '管理员',
                    key: 'admin',
                    disabled: false,
                },
            ],
        },
        {
            prefix: 'system/',
        }
    ),
    getters: {},
    actions: {
        // 租户提交
        submitTenant: ({ commit }: any, data: any) =>
            axios('/api/account/tenants/current-tenant/submit', data, 'post'),

        // 用户登录
        userLogin: ({ commit }: any, data: LoginForm) =>
            // axios('/api/account/login', data, 'post').then((res) => {
            //     commit('setUserInfo', res);
            // }),
            axios('/api/account/login', data, 'post'),
        // 用户登出
        userLogout: ({ commit }: any, data: any) => axios('/api/account/logout', data, 'post'),
        // 登录失败
        loginError: ({ commit }: any, data: any) => axios('/api/account/error', data, ),
    },
    mutations: {
        // 设置用户信息
        setUserInfo(state: any, data: any) {
            let { token, ...rest } = data;
            state.userInfo = rest;
            token && windowStorage.setSessionItem('token', token);
        },
    },
};
