import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import layout from '@/layout/index.vue';
import { useOidcLogin } from '@/libs/oidcClientConfig';
import windowStorage from '@/utils/windowStorage';
import { getUrlParam } from '@/utils/toolFn';
const routes: Array<RouteRecordRaw> = [
    {
        path: '/',
        name: 'main',
        meta: {
            title: '主页',
            hideMenu: true,
        },
        redirect: '/login',
    },

    {
        path: '/index',
        name: 'index',
        meta: {
            title: '首页内容',
            hideMenuEntry: true, // 隐藏父级，子级提升一级
        },
        component: layout,
        redirect: '/index/userManger',
        children: [
            {
                path: 'home',
                name: 'home',
                meta: {
                    title: '首页',
                    hideMenu: true, // 隐藏菜单
                },
                component: () => import('@/views/home.vue'),
            },

            {
                path: 'userManger',
                name: 'userManger',
                meta: {
                    title: '用户管理',
                    hideCard: true, // 不使用通用的card头部
                },
                component: () => import('@/views/userManager/userManager.vue'),
            },
            {
                path: 'applicationManager',
                name: 'applicationManager',
                meta: {
                    title: '应用管理',
                    hideCard: true,
                },
                component: () =>
                    import('@/views/applicationManager/applicationManager.vue'),
            },
            /* {
                path: 'tenantManager',
                name: 'tenantManager',
                meta: {
                    title: '租户管理',
                    hideCard: true,
                },
                component: () =>
                    import(
                        '@/views/tenantManager/tenantManager.vue'
                    ),
            }, */
            {
                path: 'identityManager',
                name: 'identityManager',
                meta: {
                    title: 'Identity资源管理',
                    hideCard: true,
                },
                component: () =>
                    import('@/views/identityManager/index.vue'),
                children: [
                    {
                        path: 'identityResource',
                        name: 'identityResource',
                        meta: {
                            title: 'identityResource',
                            hideCard: true,
                        },
                        component: () =>
                            import(
                                '@/views/identityManager/identityResource/identityResource.vue'
                            ),
                    },
                    {
                        path: 'identityServerClient',
                        name: 'identityServerClient',
                        meta: {
                            title: 'identityServerClient',
                            hideCard: true,
                        },
                        component: () =>
                            import(
                                '@/views/identityManager/identityServerClient/identityServerClient.vue'
                            ),
                    },
                    {
                        path: 'apiResource',
                        name: 'apiResource',
                        meta: {
                            title: 'ApiResource',
                            hideCard: true,
                        },
                        component: () =>
                            import('@/views/identityManager/apiResource/apiResource.vue'),
                    },
                    {
                        path: 'apiScope',
                        name: 'apiScope',
                        meta: {
                            title: 'ApiScope',
                            hideCard: true,
                        },
                        component: () =>
                            import('@/views/identityManager/apiScope/apiScope.vue'),
                    },
                ],
            },
        ],
    },
    {
        path: '/callback',
        name: 'callback',
        meta: {
            title: '授权回调',
            hideMenu: true, // 隐藏菜单
        },
        component: () => import('@/views/login/OidcCallback.vue'),
    },
    {
        path: '/login',
        name: 'login',
        meta: {
            title: '登录',
            hideMenu: true,
        },
        component: () => import('@/views/login/login.vue'),
    },
    {
        path: '/logout',
        name: 'logout',
        meta: {
            title: '登出',
            hideMenu: true,
        },
        component: () => import('@/views/login/logout.vue'),
    },
    {
        path: '/:pathMatch(.*)/',
        name: 'errorPage',
        meta: {
            hideMenu: true,
        },
        component: () => import('@/views/error.vue'),
    },
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
});

let whiteList = ['login', 'callback', 'errorPage', 'logout'];

router.beforeEach((to, from, next) => {
    const token = windowStorage.getSessionItem('token');
    if (to.meta && to.meta.title) document.title = to.meta.title as string;
    if (whiteList.indexOf(to.name as string) === -1 && !token) {
        // next({ name: 'login' });
        next();
    } else {
        let { ReturnUrl } = getUrlParam();
        /* to.name === 'login' ? (ReturnUrl ? next() : useOidcLogin()) : next(); */
        next()
    }
});

export default router;
