import { createStore } from "vuex";

const files = require.context('./modules', false, /\.(js)|(ts)$/);

// 为 store state 声明类型
export interface State {
    [key:string]: any
}
interface ModulesFiles {
    [key: string]: any
}

const subModules: ModulesFiles = {};
files.keys().forEach((key: string) => {
    subModules[key.replace(/(\.\/|\.js|\.ts)/g, '')] = files(key).default;
})

const baseViewportWidth = 375 // 基准的视口宽度
const viweprotWidth = document.documentElement.clientWidth;
const viweprotRatio = baseViewportWidth / viweprotWidth;


export default createStore<State>({
    state: {
        baseViewportWidth,
        viweprotRatio,
    },
    getters: {},
    mutations: {},
    actions: {},
    modules: subModules,
});
