

import windowStorage from '@/utils/windowStorage';

interface ProxyOptions {
    prefix?: string,
}

let defaultOptions: ProxyOptions = {
    prefix: "",
}

/**
 * @description: // 拦截对象更新缓存
 * @param {object} state 
 * @param {object} options
 * @return {}
 */
export default function storageProxy(state: object, options?: ProxyOptions) {
    let config: ProxyOptions = options && options || defaultOptions;
    return new Proxy(state, {
        get: function (target, propertyKey, receiver) {
            let keyValue = Reflect.get(target, propertyKey, receiver)
            if (Object.hasOwnProperty.call(target, propertyKey)) {
                let storeValue = windowStorage.getSessionItem(config.prefix + propertyKey.toString());
                keyValue = keyValue || storeValue;
                if (typeof keyValue == 'object' && keyValue !== null && Object.keys(keyValue).length === 0) {
                    keyValue = storeValue || keyValue;
                }
            }
            return keyValue;
        },
        set: function (target, propertyKey, value, receiver) {
            Reflect.set(target, propertyKey, value, receiver)
            if (Object.hasOwnProperty.call(target, propertyKey)) {
                windowStorage.setSessionItem(config.prefix + propertyKey.toString(), value);
            }
            return true;
        }
    })
}