import axios from '@/libs/request';
import storageProxy from '@/utils/storageProxy';

export default {
    namespaced: true,
    state: storageProxy(
        {
            
        },
        {
            prefix: 'tenant/',
        }
    ),
    actions: {
        //  查询所有分页资源
        getTenantList:({ commit }: any,data: object) => axios('api/user/multi-tenancy/tenantst',data),

        //  查询所有身份资源
        getIdentityresourceAllList:() => axios('api/user/multi-tenancy'),

        // 新增 身份
        addtIdentityresource:({ commit }: any,data: object) => axios('api/user/multi-tenancy',data,'post'),

        // 编辑 身份
        editIdentityresource:({ commit }: any,data: object) => axios('api/user/multi-tenancy',data,'put'),

        // 删除 身份
        deleteIdentityresource:({ commit }: any,data: object) => axios('api/user/multi-tenancy',data,'delete'),
    },
    mutations: {
        // 设置角色列表
        setRoleList(state: any, data: any) {
            state.roleList = data;
        },
    },
};
