import axios from '@/libs/request';
import storageProxy from '@/utils/storageProxy';
import resources from './resources';

export default {
    namespaced: true,
    state: storageProxy(
        {
            grantTypes: [
                { label: 'Implicit', value: 'Implicit' },
                { label: 'ClientCredentials', value: 'ClientCredentials' },
                { label: 'Code', value: 'Code' },
                { label: 'Hybrid', value: 'Hybrid' },
                { label: 'Password', value: 'Password' },
                { label: 'DeviceFlow', value: 'DeviceFlow' },
            ],
        },
        {
            prefix: 'identity/',
        }
    ),
    getters: {},
    actions: {
        /* identityResource */
        //  查询所有分页资源
        getIdentityresourceList: ({ commit }: any, data: object) =>
            axios('api/admin/identity-resource/page-list', data),
        //  查询所有身份资源
        getIdentityresourceAllList: () => axios('api/admin/identity-resource'),
        // 新增 身份
        addtIdentityresource: ({ commit }: any, data: object) =>
            axios('api/admin/identity-resource', data, 'post'),
        // 编辑 身份
        editIdentityresource: ({ commit }: any, data: object) =>
            axios('api/admin/identity-resource', data, 'put'),
        // 删除 身份
        deleteIdentityresource: ({ commit }: any, data: object) =>
            axios('api/admin/identity-resource', data, 'delete'),

        /* ApiResource */
        getIdentApiResourceList: ({ commit }: any, data: object) =>
            axios('api/admin/api-resource/page-list', data),
        //  查询所有身份资源
        getIdentApiResourceAllList: () =>
            axios('api/admin/api-resource/api-resources'),
        // 新增 身份
        addtIdentApiResource: ({ commit }: any, data: object) =>
            axios('api/admin/api-resource', data, 'post'),
        // 编辑 身份
        editIdentApiResource: ({ commit }: any, data: object) =>
            axios('api/admin/api-resource', data, 'put'),
        // 删除 身份
        deleteIdentApiResource: ({ commit }: any, data: object) =>
            axios('api/admin/api-resource', data, 'delete'),

        /* ApiScope */
        getIdentApiScopeList: ({ commit }: any, data: object) =>
            axios('api/admin/api-scope/page-list', data),
        //  查询所有身份资源
        getIdentApiScopeAllList: () => axios('api/admin/api-scope/find-all'),
        // 新增 身份
        addtIdentApiScope: ({ commit }: any, data: object) =>
            axios('api/admin/api-scope', data, 'post'),
        // 编辑 身份
        editIdentApiScope: ({ commit }: any, data: object) =>
            axios('api/admin/api-scope', data, 'put'),
        // 删除 身份
        deleteIdentApiScope: ({ commit }: any, data: object) =>
            axios('api/admin/api-scope', data, 'delete'),

        /* IdentityServerClient */
        getIdentIdentityServerClientList: ({ commit }: any, data: object) =>
            axios('api/admin/identity-server-client/page-list', data),
        //  查询所有身份资源
        getIdentIdentityServerClientAllList: () =>
            axios('api/admin/identity-server-client/find-all'),
        // 新增 身份
        addtIdentIdentityServerClient: ({ commit }: any, data: object) =>
            axios('api/admin/identity-server-client', data, 'post'),
        // 编辑 身份
        editIdentIdentityServerClient: ({ commit }: any, data: object) =>
            axios('api/admin/identity-server-client/basic-data', data, 'put'),
        // 删除 身份
        deleteIdentIdentityServerClient: ({ commit }: any, data: object) =>
            axios('api/admin/identity-server-client', data, 'delete'),
    },
    mutations: {},
};
