
class WindowStorage {
    localStroage: any;
    sessionStorage: any;
    expireOptions: any;
    constructor() {
        this.localStroage = window.localStorage;
        this.sessionStorage = window.sessionStorage;
        this.expireOptions = {
            // expireSwitch: false,
            // expire: 1000 * 3600 * 24 * 7,
            expire: 0,
            saveDate: 0
        }
    }
    /* ***************************** localStorage 缓存 */
    /**
     * @description: 设置永久缓存
     * @param {string} key
     * @param {any} data
     * @param {object} option
     * @return {}
     */
    setLocalItem(key: string, data: any, option: object | null): void {
        let itemData = option && { ...this.expireOptions, ...option, data } || { ...this.expireOptions, data };
        if (itemData.expire) {
            itemData.saveDate = new Date().getTime();
        }
        let itemStr = JSON.stringify(itemData);
        this.localStroage.setItem(key, itemStr);
    }
    /**
     * @description: 获取local缓存
     * @param {string} key
     * @return {any}
     */
    getLocalItem(key: string): any {
        return this.getExpireItem(key, this.localStroage);
    }
    /**
     * @description: 删除local中key缓存
     * @param {string} key
     * @return {}
     */
    deleteLocalItem(key: string): void {
        key && this.localStroage.removeItem(key)
    }
    /**
     * @description: 清除所有lcoal
     * @return {}
     */
    clearLocalAll(): void {
        this.localStroage.clear();
    }

    /* *************************** sessionStorage 缓存 */

    /**
     * @description: 设置Session缓存
     * @param {string} key
     * @param {any} data
     * @return {}
     */
    setSessionItem(key: string, data: any): void {
        let itemStr = JSON.stringify({ data });
        this.sessionStorage.setItem(key, itemStr);
    }
    /**
     * @description: 获取Session缓存
     * @param {string} key
     * @return {any}
     */
    getSessionItem(key: string): any {
        return this.getExpireItem(key, this.sessionStorage);
    }
    /**
     * @description: 删除session中key的缓存
     * @param {string} key
     * @return {}
     */
    deleteSeesionItem(key: string): void {
        key && this.sessionStorage.removeItem(key)
    }
    /**
     * @description: 清除 所有session缓存 
     * @return {}
     */
    clearSessionAll(): void {
        this.sessionStorage.clear();
    }

    /* *********************  */
    /**
     * @description: 获取缓存Item
     * @param {string} key
     * @param {any} storage
     * @return {any}
     */
    getExpireItem(key: string, storage: any): any {
        let result: any = null;
        try {
            let itemValue = storage.getItem(key);
            let itemData = JSON.parse(itemValue);
            if (itemData && itemData.expire && itemData.saveDate) {
                let nowTime = new Date().getTime();
                if (nowTime < (itemData.saveDate + itemData.expire)) {
                    result = itemData.data;
                } else {
                    result = null;
                }
            } else {
                result = itemData && itemData.data;
            }
        } catch (err) {
            console.log(err, '缓存JSON转化失败');
        }
        return result;
    }
}


export default new WindowStorage();
