import axios from '@/libs/request';
import storageProxy from '@/utils/storageProxy';
import windowStorage from '@/utils/windowStorage';
const { apiConfig, env } = require('@/envConfig');
export default {
    namespaced: true,
    state: storageProxy(
        {
            userInfo: {},
            readerInfo: {},
        },
        {
            prefix: 'user/',
        }
    ),
    getters: {},
    actions: {
        // 微信用户登录
        wxUserLogin: ({ commit }: any, code: string) =>
            axios(
                '/api/app/account/wx-login',
                {
                    code,
                },
                'post'
            ).then((res) => {
                commit('setUserInfo', res);
            }),
        // 微信小程序登录
        wxMiniUserLogin: ({ commit }: any, code: string) =>
            axios(
                '/api/app/account/wx-login-mini-app',
                {
                    code,
                },
                'post'
            ).then((res) => {
                commit('setUserInfo', res);
            }),
        // 获取我的读者证信息
        myReaderCardInfo: ({ commit }: any) =>
            axios('/api/app/reader-card/my-reader-card-info', {}).then(
                (res) => {
                    commit('setReaderInfo', res);
                }
            ),
        // 办证
        registeLibAccount: ({ rootState }: any) =>
            axios(
                '/api/app/reader-card/registe-lib-account',
                rootState.system.registeInfo,
                'post'
            ),
        // 修改密码
        changePassword: (store: any, data: any) =>
            axios('/api/app/reader-card/change-password', data, 'post'),
        // 绑定读者证
        bindingAccount: (store: any, data: any) =>
            axios('/api/app/reader-card/binding-account', data, 'post'),
        // 解绑读者证`
        unBindingAccount: (store: any, data: any) =>
            axios('/api/app/reader-card/un-binding-account', data, 'post'),

        // 获取用户的借阅信息
        myBorrowBooks:(store: any) =>
            axios('/api/app/reader-card/my-borrow-books'),
    },
    mutations: {
        // 设置用户数据
        setUserInfo(state: any, data: any) {
            state.userInfo = data;
            windowStorage.setSessionItem('token', data.token);
        },
        // 设置读者信息
        setReaderInfo(state: any, data: any) {
            let info = (data[0] && data[0]) || {};
            if (Object.keys(info).length > 0) {
                info.qrCodeUrl = `${apiConfig[env]}${info.qrCodeUrl}`;
                info.secretCardNumber = info.idCardNumber.replace(
                    /(\d{4}).+([\d\w]{4}$)/,
                    '$1**********$2'
                );
            }

            state.readerInfo = info;
        },
    },
};
